import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Mission from "../views/Mision.vue";
import Vision from "../views/Vision.vue";
import Team from "../views/Team.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
 

  {
    path: "/mission",
    name: "Mission",
    component: Mission
  },

   {
    path: "/team",
    name: "Team",
    component: Team
  },

  {
    path: "/vision",
    name: "Vision",
    component: Vision
  },
 
  
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
